import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.css'
import QrReader from 'modern-react-qr-reader'
import ReactHTMLTableToExcel from 'react-html-table-to-excel';

function App() {
  const [data, setData] = useState([]);
  const [urlSeen, setUrlSeen] = useState(new Set());
  const colNames = ['Coil No.', 'Order Thick', 'Order Width', 'Length', 'Net Weight', 'Color Code'];
  // QrReader call after scan
  const handleScan = (data) => {
    if (data) {
      if (urlSeen.has(data)) {
        console.log('Already scaned!');
      } else {
        fetchUrl(data);
      }
    }
  }
  // mark url as seen
  const updateSeen = (url) => {
    var newUrlSeen = new Set(urlSeen);
    newUrlSeen.add(url);
    setUrlSeen(newUrlSeen);
  }
  // CORS fetch html text from qr code
  const fetchUrl = (url) => {
    fetch('https://cors-anywhere.herokuapp.com/' + url, {
      method: 'POST',
      headers: {
        'Accept-Language': 'en-US,en;q=0.5'
      }
    }
    ).then((data) => {
      if (data.status === 200) {
        console.log(data);
        data.text().then(
          (s) => {
            s = s.replace(/<\s*br\s*>/g, ' ');
            if (!urlSeen.has(data)) getInfo(s);
            updateSeen(url);
          }
        ).catch((error) => { console.log('Error: ', error); });
      }
    }).catch((error) => {
      console.log('Error: ', error);
    });
  }
  // use regexp find value of attributes
  const tabelDate = (html, colName) => {
    const re = new RegExp(`${colName}<[^>]*>[^<]*<[^>]*>([^<]*)<`);
    const match = html.match(re);
    if (match && match[1]) return match[1];
    return '';
  }
  // get product information, push to array 'data'
  const getInfo = (html) => {
    var hashmap = {}
    colNames.forEach((colName) => {
      if (colName !== 'Color Code') hashmap[colName] = tabelDate(html, colName)
      else hashmap[colName] = tabelDate(html, '顏色編號')
    })
    var newData = [...data];
    newData.push(hashmap);
    setData(newData);
  }
  // row component
  const Row = ({ row }) => {
    return (<>
      {colNames.map((colName, idx) => {
        return (<td key={idx}>
          {row[colName]}
        </td>)
      })}
    </>);
  }

  return (
    <>
      <div>scan QR code</div>
      <QrReader
        onScan={handleScan}
        onError={err => { console.error(err) }}
        style={{ width: '100%' }}
        facingMode={'environment'}
      />
      <table className="table table-striped" id="table-to-xls">
        <thead>
          <tr>
            <th>Coil No.</th>
            <th>Order Thick</th>
            <th>Order Width</th>
            <th>Length</th>
            <th>Net Weight</th>
            <th>Color Code</th>
          </tr>
        </thead>
        {data.map((row, idx) => {
          return (<tbody key={idx}>
            <tr>
              <Row row={row} />
            </tr>
          </tbody>)
        })}
      </table >

      <ReactHTMLTableToExcel
        id="test-table-xls-button"
        className="download-table-xls-button"
        table="table-to-xls"
        filename="coil"
        sheet="tablexls"
        buttonText="Download as XLS" />
    </>
  );
}


export default App;